
export const formatter = {
  getTokboxConfig: () => (videoCallModel) => {
    const state = {};
    return {
      apiKey: videoCallModel.getSectionKey(),
      sessionId: videoCallModel.getSectionId(),
      token: videoCallModel.getSectionToken(),

      signal: {
        data: 'data',
        type: 'init',
        to: ''
      },
      ref: (ref) => {
        state.ref = ref;
      },

      eventHandlers: {
        sessionConnected: (ev) => {
          console.log('sessionConnected, caller', ev);
          // ref.current.connection = ev.connection;
          setInterval(() => {
            state.ref && state.ref.signal({ type: 'healthcheck' });
          }, 5000);
        },
        sessionDisconnected: (ev) => {
          console.log('disconnected', ev, ev.connectionId);
          videoCallModel.disConnected();
        },
        sessionReconnected: (ev) => {
          console.log('sessionReconnected', ev);
          videoCallModel.reConnected();
        },
        sessionReconnecting: (ev) => {
          console.log('sessionReconnecting', ev);
          videoCallModel.reConnecting();
        },
        streamCreated: (ev) => {
          console.log('streamCreated', ev);
          videoCallModel.startCall(ev.connectionId);
          videoCallModel.startTimer();
        },
        connectionCreated: (ev) => {
          // _.set(ref.current.peerIds, ev.connectionId, ev.connectionId);
          // on peer connect, change callstate to ongoing
          console.log('connectionCreated', ev.connectionId);
          videoCallModel.startCall(ev.connectionId);
          videoCallModel.startTimer();
        },
        connectionDestroyed: (ev) => {
          console.log('connectionDestroyed', ev);
          // ref.current.peerIds[ev.connectionId] = null;
          // auto end call
          // if(!_.compact(_.values(ref.current.peerIds)).length) {
          videoCallModel.endCall();
          // }
        },
        signal: (ev) => {
          console.log('signal', ev);
        }
      }
    };
  },

  getTokboxSubscriberConfig: () => (videoCallModel) => {
    const state = {};
    return {
      eventHandlers: {
        videoDisabled: (...args) => {
          videoCallModel.toggleVideoSubscriber();
        },
        videoEnabled: (...args) => {
          videoCallModel.toggleVideoSubscriber();
        }
      }
    };
  }
};

export default formatter;
