import React from 'react';
import { bindings, hook } from '@vl/redata';
import _ from 'lodash';
import VoiceCallModel from '@uz/unitz-models/VoiceCallWebModel';
import VoiceCallFormatter from '@uz/unitz-models/VoiceCallWebModel/formatter';
import PurchaseModel from '@uz/unitz-models/PurchaseModel';
import AdvisorModel from '@uz/unitz-models/AdvisorModel';
import AdvisorProfileFormatter from '@uz/unitz-models/AdvisorProfileModel/formatter';
import CFAdvisorProfileFormatter from '@uz/unitz-models/CFAdvisorProfileModel/formatter';
import usePromiseSource from '@vl/hooks/usePromiseSource';
import { formatVND } from '@vl/mod-utils/currencyFormatter';
import useRoute from '@vl/hooks/useGbRoute';

const bindData = bindings({
  voicePage: {
    rules: [
      [
        'data',
        {
          data: {
            '@params': hook((ctx) => {
              const route = useRoute();
              const routeParams = _.get(route, 'params', {});
              const advisor_id = _.get(routeParams, 'id');
              const service_id = _.get(routeParams, 'service_id', '');
              const purchase_id = _.get(routeParams, 'purchase_id', '');
              const user_id = ctx.apply('authModel.getUserId');
              const callParams = {
                advisor_id,
                service_id,
                user_id,
                purchase_id,
              };
              return callParams;
            }),
            voiceCallModel: hook((ctx) => {
              const advisor_id = ctx.get('@params.advisor_id');
              const voiceCallModel = React.useMemo(() => {
                const voiceCallModel = VoiceCallModel.fromCall(ctx.get('@params'), ctx);
                voiceCallModel.createSession(ctx);
                return voiceCallModel;
              }, [advisor_id]);
              voiceCallModel.hookState();

              // check call rejected
              voiceCallModel.useEffect(
                () => {
                  if (voiceCallModel.getState('states.voiceCallReject')) {
                    ctx.apply('@page.onCompleted', 'rejected');
                  }
                },
                () => [voiceCallModel.getState('states.voiceCallReject')]
              );

              // check call no answer
              voiceCallModel.useEffect(
                () => {
                  if (voiceCallModel.getState('states.isNoAnswer')) {
                    ctx.apply('@page.onCompleted', 'missed');
                  }
                },
                () => [voiceCallModel.getState('states.isNoAnswer')]
              );

              // check call finish
              voiceCallModel.useEffect(
                () => {
                  if (voiceCallModel.hasCallState(VoiceCallModel.CALL_STATES.voiceCallFinish)) {
                    ctx.apply('@page.onCompleted', 'completed');
                  }
                },
                () => [voiceCallModel.getCallState()]
              );

              return voiceCallModel;
            }),

            tokbox: hook((ctx) => {
              const voiceCallModel = ctx.get('voiceCallModel');
              const tokboxConfig = VoiceCallFormatter.getTokboxConfig(ctx)(voiceCallModel);
              return tokboxConfig;
            }),

            tokboxSubscriber: hook((ctx) => {
              const voiceCallModel = ctx.get('voiceCallModel');
              const tokboxConfig = VoiceCallFormatter.getTokboxSubscriberConfig(ctx)(voiceCallModel);
              return tokboxConfig;
            }),

            '@items': hook((ctx) => {
              const { advisor_id, service_id, user_id } = ctx.get('@params');

              const advisor = usePromiseSource(
                async () => {
                  if (!advisor_id) return null;
                  try {
                    const profile = await AdvisorModel.find(
                      `where: {id: {_eq:"${advisor_id}"}}`,
                      `services(where: {id: {_eq: "${service_id}"}}) {
                        price {
                          per_unit
                          per_amount
                          price_currency
                          price_amount
                        
                          
                        }
                        id
                        service {
                          id
                          name
                          
                        }
                      }
                      profile {
                        id
                        display_name
                        avatar_url
                        created_at
                      }
                      reviews {
                        id
                        review_text
                        score
                        updated_at
                        author {
                          profile {
                            avatar_url
                            display_name
                            id
                          }
                        }
                      } review_agg {
                        avg_score
                      } `
                    );
                    return profile.toObject();
                  } catch (err) {}
                },
                null,
                [advisor_id]
              );

              const item = _.get(advisor, '0');
              const service = _.get(advisor, '0.services.0');
              const advisorProfile = _.get(advisor, '0.profile');

              return {
                id: advisor_id,
                service_id,
                advisorProfile: {
                  id: AdvisorProfileFormatter.profileId(ctx)(advisorProfile),
                  displayName: AdvisorProfileFormatter.displayName(ctx)(advisorProfile),
                  avatarUrl: AdvisorProfileFormatter.avatarUrl(ctx)(advisorProfile),
                  rating: {
                    ratingText: AdvisorProfileFormatter.reviewText(ctx)(item),
                    ratingScore: AdvisorProfileFormatter.reviewScore(ctx)(item),
                  },
                  // createdAtTxt: AdvisorProfileFormatter.createdAt(ctx)(advisor),
                },
                price: {
                  pricePerMinTxt: AdvisorProfileFormatter.pricePerMin(ctx)(service),
                  pricePerMinTitle: AdvisorProfileFormatter.pricePerMinTitle(ctx)(service),
                },
              };
            }),
            isCalling: hook((ctx) => {
              return ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallOnProgress);
            }),
            '@page': hook((ctx) => {
              const route = useRoute();
              return {
                onCompleted: (status) => {
                  const voiceCallModel = ctx.get('voiceCallModel');
                  const session_id = voiceCallModel.getSectionId();
                  const { purchase_id, advisor_id } = ctx.get('@params');
                  const params = {
                    advisor_id,
                    purchase_id,
                    session_id,
                    step: 'completed',
                    status: status || 'completed',
                  };
                  // Change URL
                  const toUrl = route.redirectUrl(params, { origin: false });
                  route.navigate(toUrl, {
                    relace: true,
                    state: { purchase_id },
                  });
                  // Change Step
                  route.setRouteState(params);
                  ctx.apply('stepModel.setStep', params.step);
                },
              };
            }),
            purchaseData: hook((ctx) => {
              const { purchase_id } = ctx.get('@params');
              const purchaseData = usePromiseSource(
                async () => {
                  if (!purchase_id) return null;
                  try {
                    const purchase = await PurchaseModel.find(
                      `where: {id: {_eq: "${purchase_id}"}}`,
                      `
                      services {
                        id
                        price_amount
                      }
                      discounts {
                        currency
                        percentage
                        amount
                        id
                        type
                      }
                      `
                    );
                    return purchase.toObject();
                  } catch (err) {}
                },
                null,
                [purchase_id]
              );
              const bill = CFAdvisorProfileFormatter.amountPayment(ctx)({
                price: { price_amount: _.get(purchaseData, '0.services.0.price_amount') },
                discounts: _.get(purchaseData, '0.discounts'),
              });
              return {
                totalAfter: _.get(bill, 'totalAfter') ? formatVND(_.get(bill, 'totalAfter')) : 0,
                totalBefore: _.get(bill, 'totalBefore') ? formatVND(_.get(bill, 'totalBefore')) : 0,
                discount: _.get(bill, 'discount') ? formatVND(_.get(bill, 'discount')) : 0,
              };
              // return items;
            }),
          },
        },
      ],
    ],
  },
  sectionVoiceActions: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => {
            return (
              true ||
              ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallConnecting) ||
              ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallOnProgress)
            );
          }),
        },
      ],
    ],
  },
  voiceCallFinish: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallFinish)),
        },
      ],
      [
        'data',
        {
          data: {},
        },
      ],
    ],
  },
  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
