import React from 'react';
import _ from 'lodash';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import Loadable from '@loadable/component';

import PublisherHandler from './PublisherHandler';

const OTSession = Loadable(() => import('./OpenTok/OTSession'));
const OTPublisher = Loadable(() => import('./OpenTok/OTPublisher'));
const OTStreams = Loadable(() => import('./OpenTok/OTStreams'));
const OTSubscriber = Loadable(() => import('./OpenTok/OTSubscriber'));

function areEqual(prevProps, nextProps) {
  return _.isEqual(prevProps.layout, nextProps.layout);
}

const CallSession = React.memo(({ voiceCallModel, layout }) => {
  const isSSR = typeof window === 'undefined';
  if (isSSR) return null;
  return (
    <DIV forceCtx>
      <OTSession {...ctx.get('tokbox')}>
        <PublisherHandler>
          <OTPublisher
            className="w-full h-full bg-transparent"
            properties={{
              cameraPosition: voiceCallModel.useState('cameraPosition'),
              publishAudio: !voiceCallModel.useState('myState.micMute'),
              publishVideo: false,
              width: '100%',
              height: '100%',
              showControls: false,
              style: {
                backgroundImageURI: ctx.get('@items.userProfile.avatarUrl'),
              },
            }}
            eventHandlers={{
              error: (...args) => {
                console.log('error', args);
              },
              otrnError: (...args) => {
                console.log('otrnError', args);
              },
            }}
          ></OTPublisher>
        </PublisherHandler>
        <div
          style={{
            ..._.pick(layout, ['width', 'height']),
          }}
        >
          <OTStreams>
            <div
              className="absolute bg-transparent"
              style={{
                ..._.pick(layout, ['width', 'height']),
                ...(!ctx.apply('voiceCallModel.useState', 'myState.collapse')
                  ? {
                    left: 0,
                    top: 0,
                    width: layout.width / 2,
                    height: layout.height,
                  }
                  : {}),
              }}
            >
              <OTSubscriber
                className="w-full h-full bg-transparent"
                {...ctx.get('tokboxSubscriber')}
                properties={{
                  showControls: false,
                  height: '100%',
                  width: '100%',
                  style: {
                    backgroundImageURI: ctx.get('@items.advisorProfile.avatarUrl'),
                  },
                }}
              />
            </div>
          </OTStreams>
        </div>
      </OTSession>
    </DIV>
  );
}, areEqual);

const View8 = () => {
  return (
    <DIV className="callSession">
      {ctx.apply('LAYOUT.useLayout', { layout: 'AdvisorVoiceCallStatus' }, ({ layout }) => {
        if (!layout) return null;
        if (!ctx.get('tokbox.sessionId')) return null;
        const voiceCallModel = ctx.get('voiceCallModel');
        return (
          <CallSession
            voiceCallModel={voiceCallModel}
            layout={layout}
            advisorAvatarUrl={ctx.get('@items.advisorProfile.avatarUrl')}
            userAvatarUrl={ctx.get('@items.userProfile.avatarUrl')}
          />
        );
      })}
    </DIV>
  );
};

export default View8;
