import { bindings, hook } from '@vl/redata';
import VoiceCallModel from '@uz/unitz-models/VoiceCallWebModel';

const bindData = bindings({
  voiceComponent: {
    rules: [
      [
        'data',
        {
          data: {},
        },
      ],
    ],
  },
  voiceCallIniting: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallIniting)),
        },
      ],
    ],
  },
  voiceCallConnecting: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallConnecting)),
        },
      ],
    ],
  },
  voiceCallIncomming: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallIncomming)),
        },
      ],
    ],
  },
  voiceCallTaking: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallTaking)),
        },
      ],
    ],
  },
  voiceCallOnProgress: {
    rules: [
      [
        'display',
        {
          display: hook(
            (ctx) => ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallOnProgress)
              && ctx.apply('voiceCallModel.isConnected')
          ),
        },
      ],
    ],
  },

  voiceCallOnProgressOffCamera: {
    rules: [
      [
        'display',
        {
          display: hook(
            (ctx) => ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallOnProgress)
              && !(
                ctx.apply('voiceCallModel.useState', 'remoteState.voiceMute') && ctx.apply('voiceCallModel.isConnected')
              )
          ),
        },
      ],
    ],
  },

  voiceCallDisconnect: {
    rules: [
      [
        'display',
        {
          display: hook(
            (ctx) => ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallFinish)
              && ctx.apply('voiceCallModel.isDisconnected')
          ),
        },
      ],
    ],
  },

  sessionReconnect: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('voiceCallModel.isConnecting')),
        },
      ],
    ],
  },

  callSession: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('voiceCallModel.hasCallState', VoiceCallModel.CALL_STATES.voiceCallFinish)),
        },
      ],
    ],
  },
});

export default bindData;
