import _ from 'lodash';

import fbFnsClient from '@vl/mod-clients/fibGatsbyFns';
import fibApp from '@vl/mod-clients/fibGatsby';

import { BaseModel } from '@uz/unitz-models/BaseModel';

import Stream from '@vl/mod-utils/Stream';
import moment from 'moment';
import { durationFormatter } from '@vl/mod-utils/currencyFormatter';

import VoiceCallState from './VoiceCallState';
import { INTERNET_STATES } from './constants';

const NOTIF_INTERVAL = 2000;
// const channelId = 'unitz-notifee-video-channel-2';
// const soundFile = 'ringing';
// const vibrationPattern = [300, 500];

const ACTIONS = {
  TAKE: 'ACCEPT',
  REJECT: 'DECLINE',
};

const ACTION_HANDLERS = {
  TAKE: null,
  REJECT: null,
};

class InstanceMap {
  items = {};

  add(instance) {
    const id = instance.get('id');
    if (!id) return null;
    this.items[id] = instance;
  }

  get(id) {
    return this.items[id];
  }
}

// export class VoiceCallModel extends BaseModel {
export class VoiceCallModel extends BaseModel {
  static instances = new InstanceMap();

  static CALL_STATES = {
    voiceCallConnecting: 'voiceCallConnecting',
    voiceCallOnProgress: 'voiceCallOnProgress',
    voiceCallIncomming: 'voiceCallIncomming',
    voiceCallFinish: 'voiceCallFinish',
    voiceCallIniting: 'voiceCallIniting',
    voiceCallTaking: 'voiceCallTaking',
    voiceCallReject: 'voiceCallReject',
  };

  static INTERNET_STATES = {
    ...INTERNET_STATES,
  };

  state = {
    callState: VoiceCallModel.CALL_STATES.voiceCallIniting,
    videoMute: false,
    cameraPosition: 'front',
    startTime: null,
    msg: '',
    remoteState: {
      videoMute: false,
    },
    localState: {
      videoMute: false,
    },
  };

  constructor(...args) {
    super(...args);
    this.constructor.initState(this);

    const stream = new Stream();

    this.set({
      stream,
      resolvers: {
        user: async () => {
          const user_id = this.get('user_id');
          console.log('user_id', user_id);
          // const user = await UserModel.fromId({ id: user_id });
          // return user;
          return { id: user_id };
        },
        advisor: async () => {
          const advisor_id = this.get('advisor_id');
          console.log('advisor_id', advisor_id);
          // const instance = await AdvisorModel.fromId({ id: advisor_id });
          // return instance;
          return { id: advisor_id };
        },
      },
    });

    // configure stream reducer
    stream.on('notify', async (msg) => {
      // reschedule notify stream
      const notifTimer = setTimeout(() => {
        stream.emit('notify', msg);
      }, NOTIF_INTERVAL);
      this.set({ notifTimer });
    });

    stream.on('stopNotify', async () => {
      // remove timer
      const notifTimer = this.get('notifTimer');
      if (notifTimer) {
        clearTimeout(notifTimer);
        this.set({ notifTimer: null });
      }
      // await notifee.cancelNotification(notifId);
    });
    // effect to start call
    this.useEffect(
      () => {
        if (this.hasCallState(VoiceCallModel.CALL_STATES.voiceCallTaking)) {
          this.startCall();
          this.startTimer();
        }
      },
      () => [this.getCallState()]
    );
    this.useEffect(
      () => {
        if (this.getState('states.startTime') && this.hasCallState(VoiceCallModel.CALL_STATES.voiceCallConnecting)) {
          this.startCall();
          this.startTimer();
        }
      },
      () => [this.getState('states.startTime')]
    );

    // effect to end call
    this.useEffect(
      () => {
        if (
          this.getState('states.endTime')
          && this.getState('states.voiceCallComplete')
          && !this.hasCallState(VoiceCallModel.CALL_STATES.voiceCallFinish)
        ) {
          this.setState({
            endTime: moment(),
            callState: VoiceCallModel.CALL_STATES.voiceCallFinish,
          });
        }
      },
      () => [this.getState('states.endTime'), this.getState('states.voiceCallComplete')]
    );
    this.state.once('onDestroy', () => {
      this.isMounted = false;
      this.stopNotifySession();

      console.log('getCallState', this.getCallState());
      if (
        this.hasCallState(VoiceCallModel.CALL_STATES.voiceCallConnecting)
        || this.hasCallState(VoiceCallModel.CALL_STATES.voiceCallIncomming)
        || this.hasCallState(VoiceCallModel.CALL_STATES.voiceCallIniting)
      ) {
        this.setState('states.voiceCallReject', true);
      }
    });
  }

  static create(data, opts) {
    console.log('voice create');
    const instance = new VoiceCallModel();
    const id = _.get(data, 'data.session_id');

    instance.set({ ...opts, msg: data, id });

    this.instances.add(instance);
    //
    return instance;
  }

  static fromMessage(msg, ctx) {
    const id = _.get(msg, 'data.session_id') || _.get(msg, 'id');
    let instance = this.instances.get(id);
    if (!instance) {
      instance = this.create(msg);
    }

    const user_id = _.get(msg, 'data.user_id');

    const advisor_id = _.get(msg, 'data.advisor_id');

    const service_id = _.get(msg, 'data.service_id');

    const session_id = _.get(msg, 'data.session_id');

    if (user_id && advisor_id && service_id && session_id) {
      instance.set({
        user_id,
        advisor_id,
        service_id,
        session_id,
      });
      instance.connectStore(ctx);
    }

    instance.ctx = ctx;

    return instance;
  }

  static async fromSession({ user_id, advisor_id }) {
    try {
      const instance = await VoiceCallModel.create({ user_id, advisor_id });
      await instance.createSession();
      return instance;
    } catch (err) {
      console.log('ererer', err);
    }
  }

  static fromCall(callData) {
    try {
      const instance = VoiceCallModel.create();
      instance.set(callData);
      return instance;
    } catch (err) {
      console.log('ererer', err);
    }
  }

  connectStore(ctx) {
    // store
    const id = this.get('session_id');

    const user_id = this.get('user_id');
    const advisor_id = this.get('advisor_id');
    const service_id = this.get('service_id');

    this.state.enhance(
      new VoiceCallState(
        {
          id,
          user_id,
          advisor_id,
          service_id,
        },
        ctx
      )
    );

    console.log('connectStore', ctx);
  }

  getSectionId() {
    const msg = this.get('msg');
    const id = _.get(msg, 'data.session_id');
    return id;
  }

  getSectionToken() {
    const msg = this.get('msg');
    const token = _.get(msg, 'data.session_token');
    return token;
  }

  getSectionKey() {
    const msg = this.get('msg');
    const key = _.get(msg, 'data.session_key');
    return key;
  }

  async createSession(ctx) {
    // return null;

    this.setState({
      msg: 'AdvisorVoiceCall.initingStatusTxt',
    });

    console.log('createSession', ctx);

    try {
      const user_id = this.get('user_id');
      const advisor_id = this.get('advisor_id');
      const service_id = this.get('service_id');
      const purchase_id = this.get('purchase_id');

      const params = {
        user_id,
        advisor_id,
        service_id,
        purchase_id,
      };

      const data = await fbFnsClient.getClient().post('tasks-createSession', params);

      console.log({ data, params });

      if (_.get(data, 'message.length', 0) > 0) {
        return this.setState({
          msg: { text: _.get(data, 'message') },
        });
      }
      // await new Promise(res => setTimeout(res, 500));
      // const data = {"advisor_id": "9KGhxtVGdPMD4VRIuZw25Iwo8x33", "session_duration": "360", "session_id": "2_MX40NTc4ODY5Mn5-MTYxNjI5ODQ3MTM1Nn5EQlB3WHhOL1VSaGtWY2FsRkJrWnMrT01-fg", "session_key": "45788692", "session_token": "", "user_id": "9KGhxtVGdPMD4VRIuZw25Iwo8x33"};
      this.set('session_id', _.get(data, 'session_id'));

      this.connectStore(ctx);
      this.ctx = ctx;

      this.set({ msg: { data } });
      // update call state
      this.setState({
        msg: 'AdvisorVoiceCall.ringingStatusTxt',
        callState: VoiceCallModel.CALL_STATES.voiceCallConnecting,
      });

      return data;
    } catch (err) {
      console.log('create session error', err);
      this.setState({
        msg: { text: err.toString() },
      });
    }
  }

  async notifySession() {
    this.set({ stopNotifySession: false });

    const msg = this.get('msg');

    const notifId = _.get(msg, 'data.session_id');
    this.set({ notifId, msg });
    this.get('stream').emit('notify', msg);
    this.watchNotifTimeout();
  }

  async stopNotifySession() {
    this.set({ stopNotifySession: true });
    this.get('stream').emit('stopNotify');
  }

  watchNotifTimeout() {
    const notifId = this.get('notifId');
    const watcherKey = `notifListener.${notifId}`;
    if (!this.get(watcherKey)) {
      const db = fibApp.getClient().firestore();
      const docRef = db.collection('videocalls').doc(notifId);
      const disposers = [];
      disposers.push(
        docRef.onSnapshot((docData) => {
          const data = docData.data();
          if (_.get(data, 'states.isNoAnswer')) {
            this.stopNotifySession();
          }
        })
      );

      disposers.push(
        this.get('stream').on('stopNotify', () => {
          disposers.map((dis) => dis());
          disposers.splice(0, disposers.length);
        })
      );
      this.set(watcherKey, true);
    }
  }

  /**
   * handlers for VoiceCallModel
   *
   * @memberof VoiceCallModel
   */
  switchCamera() {
    const cameraPositions = ['front', 'back'];
    const cameraPosition = this.getState('cameraPosition');
    const currIndex = _.indexOf(cameraPositions, cameraPosition);
    const newPos = cameraPositions[(currIndex + 1) % cameraPositions.length];
    this.setState('myState.cameraPosition', newPos);
    this.setState({
      cameraPosition: newPos,
    });
  }

  toggleMic() {
    this.setState('myState.micMute', !this.getState('myState.micMute'));
  }

  toggleVideo() {
    this.setState('myState.videoMute', !this.getState('myState.videoMute'));
  }

  toggleVideoSubscriber() {
    this.setState('remoteState.videoMute', !this.getState('remoteState.videoMute'));
  }

  collapseVideo() {
    this.setState('myState.collapse', true);
  }

  separateVideo() {
    this.setState('myState.collapse', false);
  }

  reConnecting() {
    this.setState('myState.internetStatus', VoiceCallModel.INTERNET_STATES.reconnecting);
  }

  reConnected() {
    this.setState('myState.internetStatus', VoiceCallModel.INTERNET_STATES.connected);
  }

  disConnected() {
    if (this.getCallState() !== VoiceCallModel.CALL_STATES.voiceCallFinish) {
      this.setState('myState.internetStatus', VoiceCallModel.INTERNET_STATES.disconnect);
    }
  }

  isConnected() {
    return (
      this.useState('myState.internetStatus') === VoiceCallModel.INTERNET_STATES.connected
      && this.useState('otherState.internetStatus') === VoiceCallModel.INTERNET_STATES.connected
    );
  }

  isConnecting() {
    return (
      this.useState('myState.internetStatus') === VoiceCallModel.INTERNET_STATES.reconnecting
      || this.useState('otherState.internetStatus') === VoiceCallModel.INTERNET_STATES.reconnecting
    );
  }

  isDisconnected() {
    return this.useState('myState.internetStatus') === VoiceCallModel.INTERNET_STATES.disconnect;
  }

  async startCall(peerId) {
    this.setState({
      callState: VoiceCallModel.CALL_STATES.voiceCallOnProgress,
    });
  }

  takeCall() {
    this.stopNotifySession();

    this.setState({
      callState: VoiceCallModel.CALL_STATES.voiceCallTaking,
    });
  }

  startTimer() {
    if (!this.getState('states.startTime')) {
      this.setState('states.startTime', moment().valueOf());
    }
    this.setState({
      startTime: moment(this.getState('states.startTime')),
    });
  }

  endCall() {
    if (
      this.hasCallState(VoiceCallModel.CALL_STATES.voiceCallConnecting)
      || this.hasCallState(VoiceCallModel.CALL_STATES.voiceCallIncomming)
    ) {
      this.setState('states.voiceCallReject', true);

      return;
    }

    this.setState('states.endTime', moment().valueOf());
    this.setState('states.voiceCallComplete', true);
  }

  startIncomingCall() {
    this.setState({
      callState: VoiceCallModel.CALL_STATES.voiceCallIncomming,
    });
  }

  rejectCall() {
    // try to go to previous screen
    // routes.goBack();
    this.setState('states.voiceCallReject', true);
  }

  /**
   * getter for VoiceCallModel
   *
   * @returns
   * @memberof VoiceCallModel
   */
  getCallState() {
    const callState = this.useState('callState');
    console.log('---hasCallState', callState);
    return callState;
  }

  hasCallState(state) {
    return this.getCallState() === state;
  }

  getCallDuration() {
    const startTime = this.useState('startTime');
    if (!startTime) return '';

    const now = moment();
    const duration = moment.duration(now.diff(startTime));
    return durationFormatter(duration.asMilliseconds()); // `${_.padStart(duration.minutes(), 2, '0')}:${_.padStart(duration.seconds(), 2, '0')}`;
  }

  getCallDurationTotal(format = 'MM') {
    const startTime = this.getState('startTime');
    const endTime = this.getState('endTime');
    if (!startTime || !endTime) return '';

    const duration = moment.duration(endTime.diff(startTime));
    return durationFormatter(duration.asMilliseconds()); // `${_.padStart(duration.minutes(), 2, '0')}:${_.padStart(duration.seconds(), 2, '0')}`;
  }

  getCallMaxDuration() {
    const msg = this.get('msg');
    const durationSecondsStr = _.get(msg, 'data.session_duration');
    const duration = moment.duration(durationSecondsStr, 'seconds');

    return durationFormatter(duration.asMilliseconds()); // `${_.padStart(duration.minutes(), 2, '0')}:${_.padStart(duration.seconds(), 2, '0')}`;
  }

  static async onForegroundEvent(ev) {
    const { type, detail } = ev;
    // if (type === EventType.ACTION_PRESS) {
    //   const action = _.get(detail, 'pressAction.id');
    //   const msg = _.get(detail, 'notification');
    //   const instance = await VoiceCallModel.fromMessage(msg);
    //   if (instance) {
    //     const handlers = instance.get('actions');
    //     const handler = _.get(handlers, [action]);
    //     if (_.isFunction(handler)) handler(msg);
    //   }
    // }
  }

  static async onBackgroundEvent(ev) {
    const { type, detail } = ev;
    // if (type === EventType.ACTION_PRESS) {
    //   console.log('type, detail', type, detail.pressAction);
    // } else {
    //   console.log('tyatyatya', type);
    // }
  }
}

