const ControlButton = ({
  className, icon, text, horizontal, ...rest
}) => {
  return (
    <div
      {...rest}
      className={`flex items-center justify-center cursor-pointer text-white500 ${
        horizontal ? 'flex-row' : 'flex-col'
      } ${className}`}
    >
      {icon}
      {!!text && <div className="mb-0 font-light text-normal">{text}</div>}
    </div>
  );
};

